import { Component, EventEmitter, Input, Output } from '@angular/core';
import { DetailedExternalAccount } from '@backbase/data-ang/payment-order-a2a';
import { payverisStatuses } from '../../helpers/constants';

@Component({
  selector: 'bb-a2a-details-header',
  templateUrl: './details-header.component.html',
})
export class AccountDetailModalHeaderComponent {
  statuses = payverisStatuses;

  /**
   * External account details
   */
  @Input() account!: DetailedExternalAccount;

  /**
   * Activate account attempts exceeded flag
   * Default value is false
   */
  @Input() attemptsExceeded = false;

  /**
   * Close modal event emitter
   */
  @Output() closeModal = new EventEmitter<void>();

  closeDetailModal() {
    this.closeModal.emit();
  }
}
